                        label="名称"
                        align="center"
                        width="120">
                      </el-table-column>
                      <el-table-column
                        prop="type"
                        label="类型"
                        align="center">
                      </el-table-column>
                      <el-table-column
                        prop="versionNumber"
                        label="版本号"
                        align="center">
                      </el-table-column>
                      <el-table-column
                        prop="graphical"
                        label="流程图形"
                        align="center">
                      </el-table-column>
                      <el-table-column
                        prop="deploymentTime"
                        label="部署时间"
                        align="center">
                      </el-table-column>
                      <el-table-column
                        label="操作"
                        width="160"
                        align="center">
                        <template>
                          <img class="margin-right-3px pointer" src="../../../assets/images/hangUp.png" alt="挂起">
                          <img class="margin-right-3px pointer" src="../../../assets/images/activation.png" alt="激活">
                          <img class="margin-right-3px pointer" src="../../../assets/images/Transformation.png" alt="转换">
                          <img class="pointer" src="../../../assets/images/delete.png" alt="删除">
                        </template>
                      </el-table-column>
                    </el-table>
                  </template>
                  
                  <!-- <iframe ref="iframe" v-show="fromVisible" src="../../../../static/processDesigner/index.html" width="100%" height="715px"></iframe> -->
                  <!-- <iframe ref="iframe" v-show="fromVisible" src="/workflow/front-ser../processDesigner/index.html" width="100%" height="715px"></iframe> -->
                  
