    mounted() {
      
    },
    methods: {
      //显示隐藏表格流程图
      isShowTable(){
        this.fromVisible = true;
        this.tableVisible = false;
      },
      handleSizeChange(val) {
        console.log(`每页 ${val} 条`);
      },
      handleCurrentChange(val) {
        console.log(`当前页: ${val}`);
      }
    }
  };
</script>


<style lang="less" scoped>

  .eol-table-box-right,
  .eol-table-box-right-inner,
  .eol-table-container{
      height:100%;
  }
  #form-model{
    padding:20px 20px 0px 20px;
    border-bottom: 1px solid #BAC4CC;
  }
  //按钮
  .model-btn{
    padding:10px 20px;
  }
  //表格
  .modelList-table{
     padding:0px 20px;
  }
  //分页
  .pagination-block{
    margin:20px;
    padding:10px 0px;
    text-align: center;
    background: #e5f5fd;
  }
</style>



